<template>
  <div class="div-vp">
    <el-container direction="horizontal">
      <el-aside class="el-aside-vp" width="170px">
        <div class="div-vp div-vp-c">左</div>
      </el-aside>
      <el-container class="el-container-vpM" direction="vertical">
        <el-header class="el-header-vp" height="60px">
          <el-row justify="space-between" type="flex" class="oneRow-vp">
            <el-col :span="6" class="el-col-vp el-col-vp-l" align="middle">
              <div class="div-vp div-vp-c">中-上1</div>
            </el-col>
            <el-col :span="6" class="el-col-vp el-col-vp-l" align="middle">
              <div class="div-vp div-vp-c">中-上2</div>
            </el-col>
            <el-col :span="6" class="el-col-vp el-col-vp-l" align="middle">
              <div class="div-vp div-vp-c">中-上3</div>
            </el-col>
            <el-col :span="6" class="el-col-vp" align="middle">
              <div class="div-vp div-vp-c">中-上4</div>
            </el-col>
          </el-row>
        </el-header>
        <el-main class="el-main-vpB" align="middle">
          <div class="div-vp div-vp-c">中-中</div>
        </el-main>
        <el-footer class="el-footer-vp" align="middle">
          <div class="div-vp div-vp-c">中-下</div>
        </el-footer>
      </el-container>
      <el-aside class="el-aside-vp" width="200px" align="middle">
        <div class="div-vp">右</div>
      </el-aside>
    </el-container>
  </div>
</template>
<script>
export default {
  data() {
    return {
      gt: 0, // 最高温(double)
      lt: 0, // 最低温(double)
      saveing: false // 是否正在保存
    }
  },
  created() {
    // console.log(new Date().format("yyyy-MM-dd hh:mm:ss"))
    // console.log(this.seekTime)
    this.isToken = JSON.parse(sessionStorage.getItem('token'))
    this.getLimit()
  },
  methods: {
    getLimit() {
      this.$http
        .get('/api' + '/device/find/limit')
        .then(res => {
          if (res.data && res.data.code === 0 && res.data.data) {
            this.configGd.limit = [res.data.data.lt, res.data.data.gt]
            this.lt = res.data.data.lt
            this.gt = res.data.data.gt
          }
        })
        .catch(err => {
          this.$message.error({
            message: '查询体温报警阀值失败'
          })
        })
    },
    setLimit() {
      this.saveing = true
      this.$http
        .get('/api' + '/device/set/limit' + '?lt=' + this.lt + '&gt=' + this.gt)
        .then(res => {
          this.saveing = false
          console.log('res.data:', res.data)
          if (res.data && res.data.code === 0) {
            this.$message({
              message: '设置体温报警阀值成功',
              type: 'success'
            })
          }
        })
        .catch(err => {
          this.saveing = false
          this.$message.error({
            message: '设置体温报警阀值失败！'
          })
        })
    }
  }
}
</script>
<style lang="less" scoped>
.div-vp {
  margin: 0;
  padding: 0;
}
.div-vp-c {
  padding: 10px;
}
.div-num {
  background-color: #00162c;
}
.oneRow-vp {
  color: #3c4e86;
  padding: 0;
}
.el-col-vp {
  margin: 0 auto;
  background-color: #00162c;
  height: 60px;
  margin: 0;
}
.el-col-vp-l {
  margin-right: 10px;
}
.margin-all {
  margin: 0;
}
.el-aside-vp {
  width: 200px; //0.45rem
  background: #00162c;
}
.el-container-vpM {
  min-width: 0;
  background-color: #01101e;
  min-height: calc(100vh - 110px); //105px
  padding: 0px 10px 0px 10px;
}
.el-main-vpB {
  min-width: 0;
  margin: 10px 0px 10px 0px;
}

.el-header-vp {
  min-width: 0;
  padding: 0;
}

.el-footer-vp {
  min-width: 0;
  background-color: #00162c;
  height: 40px;
  font-size: 13px;
}
</style>
